cart-items {
  display: block;
}

// Swatches
.swatches.grid {
  margin: 20px 0 40px;
  padding: 40px 0;
  border-top: 1px solid var(--color-foreground);
  gap: 16px;
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  @media screen and (min-width: 1024px) {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  .intro {
    grid-row: span 2;    
    @media screen and (min-width: 1024px) {
      grid-row: span 2;
    }
  }
  .cart-item {
    .flex {
      flex-wrap: nowrap;
      gap: 16px;
      .text {
        position: relative;
        padding: 16px 44px 0 16px;
        background-color: rgba(var(--color-lightpink-rgb),0.5);
        font-family: var(--font-body-family);
        flex: 1 1 65%;
        > p {
          margin: 0; 
        }
        p > span {
          text-transform: uppercase;
          font-weight: bolder;
        }
        .remove-button {
          position: absolute;
          top: 2px;
          right: 4px;
          .button {
            width: 40px;
            height: 40px;
            min-width: 40px;
            min-height: 40px;
          }
        }
      }
      .image {
        position: relative;
        flex: 0 0 110px;
        img {
          display: block;
          object-fit: cover;
        }
      }
    }
  }
}


// Faker Table
.cart-items-table {
  margin: 20px 0 40px;
  width: 100%;
  padding: 40px 0;
  border-top: 1px solid var(--color-foreground);
  border-bottom: 1px solid var(--color-foreground);
  .inline_title {
    font-size: 87.5%;
    display: block;
    text-align: left;
    padding-bottom: 8px;
    @media screen and (min-width: 1024px) {
      display: none;
      visibility: hidden;
    }
  }
}

.table.grid {
  grid-template-columns: 120px 1fr 1fr 1fr;
  gap: 0;
  @media screen and (min-width: 1024px) {
    grid-template-columns: 120px 30% 1fr 1fr 1fr 1fr;
    gap: 16px;
  }
}

.table_header {
  margin-bottom: 16px;
  > div {
    align-self: end;
  }
  .title {
    grid-column: span 2;
    h3.h2 {
      line-height: 1;
    }
  }
  .th_price, .products, .total {
    text-align: center;
    @media screen and (max-width: 1023px) {
      display: none;
      visibility: hidden;
    }
  }
  .remove {
    text-align: right;
    padding-right: 16px;
    @media screen and (max-width: 1023px) {
      display: none;
      visibility: hidden;
    }
  }
}

.table_body {
  .cart-item {
    background-color: rgba(var(--color-lightpink-rgb),0.5);
    margin-bottom: 16px;
    > div {
      align-self: center;
    }
    .cart-item__image {
      border-right: 16px solid white;
      @media screen and (max-width: 499px) {
        border-bottom: 16px solid white;
      }
    }
    
  }
}

.cart-item__quantity {
  .quantity {
    gap: 0;
    align-content: center;
    button {
      flex: 0 1 32px;
      height: 32px;
    }
  }
  @media screen and (max-width: 1023px) {
    grid-column: 3 / span 1;
    grid-row: 2;
    //padding-left: 16px;
    .quantity {
      max-width: 130px;
    }
  }
  @media screen and (max-width: 499px) {
    grid-column: 2 / span 2;
    grid-row: 2;
  }
  display: flex;
  .quantity__input {
    border-color: transparent;
  }
}

.cart-item__totals {
  position: relative;
  @media screen and (max-width: 1023px) {
    grid-column: 4 / span 1;
    grid-row: 2;
  }
}

.cart-item__details {
  padding-left: 16px;
  @media screen and (max-width: 1023px) {
    grid-column: 2 / span 2;
  }
  @media screen and (min-width: 1024px) {
    padding-left: 10%;
  }
  dl {
    margin: 8px 0 0 0;
  }
}

.cart-item__price, .cart-item__totals {
  text-align: center;
}

.cart-item__price {
  @media screen and (max-width: 1023px) {
    grid-column: 2 / span 1;
    grid-row: 2;
    padding-left: 16px;
    text-align: left;
  }
  @media screen and (max-width: 499px) {
    grid-column: 1 / span 1;
    grid-row: 2;
    padding: 32px 0 32px 16px;
  }
}

.cart-item__remove {
  padding-right: 16px;
  text-align: right;
  @media screen and (max-width: 1023px) {
    grid-column: 4 / span 1;
    grid-row: 1;
  }
}

.cart-item__image {
  display: block;
}

.cart-item__price-wrapper {
  @media screen and (max-width: 1023px) {
    padding-right: 16px;
  }
  .inline_title {
    text-align: right;
  }
  .price {
    font-size: 100%;
    font-family: var(--font-body-family);
    @media screen and (max-width: 1023px) {
      justify-content: flex-end;
    }
  }
}

.cart-item__media {
  position: relative;
  background-color: white;
  
}

.blankgrid_item {
  @media screen and (max-width: 499px) {
    display: none;
    visibility: hidden;
  }
  @media screen and (min-width: 500px) {
    grid-column: 1 / span 1;
    grid-row: 2;
    height: 100px;
    width: 100%;
    background-color: white;
  }
  @media screen and (min-width: 1024px) {
    display: none;
    visibility: hidden;
  }
}

.cart-item__link {
  display: block;
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.cart-item__name {
  color: rgb(var(--color-foreground));
  text-decoration: none;
  display: block;
}

.offer_prompt {
  margin-top: -24px;
  align-items: center;
  justify-content: flex-end;
  p {
    margin: 0;
    flex: 1;
    text-align: right;
  }
  form {
    flex: 0 1 250px;
    div {
      margin-bottom: 0;
    }
  } 
  input.button.green {
    background-color: var(--color-green);
    border-color: var(--color-green);
    color: white;
  }
}

.cart-item__discounted-prices dd {
  margin: 0;
}

.cart-item__details .discounts {
  font-size: 87.5%;
  margin: 8px 0 0 0;
}

.cart-item__old-price {
  opacity: 0.7;
}


.cart-item cart-remove-button, .cart-item .remove-button {
  display: inline-block;
  margin-left: 1rem;
}
 
@keyframes rotate-in-center {
  0% {
    transform: rotate(-360deg);
  }
  100% {
    transform: rotate(0);
  }
}
cart-remove-button, .remove-button {
  .button {
    box-shadow: none;
    min-width: 4.5rem;
    min-height: 4.5rem;
    padding: 0;
    margin: 0 0.1rem 0.1rem 0;
    border: 0;
    @media screen and (min-width: 750px) {
      // min-width: 3.5rem;
      // min-height: 3.5rem
    }
    &:hover {
      animation: rotate-in-center 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    }
  }
  .button:not([disabled]):hover {
    box-shadow: none;
  }
  .button:not([disabled]):focus-visible {
    box-shadow: none;
  }
  .icon-remove {
    height: 1.5rem;
    width: 1.5rem;
  }
}

.cart-item .loading-overlay {
  top: 0;
  left: auto;
  right: auto;
  bottom: 0;
  padding: 0;
}

@media screen and (min-width: 750px) {
  .cart-item .loading-overlay {
    right: 0;
    padding-top: 4.5rem;
    bottom: auto;
  }
}

.loading-overlay:not(.hidden) ~ * {
  visibility: hidden;
}

.cart-item__error {
  display: flex;
  align-items: flex-start;
}

.cart-item__error-text {
  order: 1;
}

.cart-item__error-text + svg {
  flex-shrink: 0;
  width: 1.2rem;
  margin-right: 0.7rem;
  margin-top: 0.1rem;
}

.cart-item__error-text:empty + svg {
  display: none;
}

.product-option {
  color: rgba(var(--color-foreground), 0.7);
  display: inline-block;
  margin-right: 8px;
  dt, dd {
    font-size: 87.5%;
  }
}

.product-option + .product-option {
  margin-top: 4px;
}

.product-option * {
  display: inline;
  margin: 0;
}

